@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #202020;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
}
.container1 {
  align-items: center;
  display: flex;
  height: 40px;
}

.container2 {
  align-items: center;
  display: flex;
  height: 40px;
  width: 53%;
}

.container3 {
  display: flex;
}

header .subcontainer {
  display: flex;
}

header {
  align-items: center;
  display: flex;
  height: 56px;
  padding: 0 16px;
  justify-content: space-between;
}

header .menu {
  height: 20px;
  padding: 8px;
  width: 20px;
}

header .logoyt img {
  height: 20px;
  padding: 18px 14px 18px 16px;
  width: 90px;
}

header .search {
  margin-right: 0px;
  width: 90%;
}

header input {
  align-items: center;
  border: 1px #373737 solid;
  background-color: #121212;
  color: white;
  display: flex;
  font-size: 17px;
  height: 40px;
  padding-left: 10px;
  width: 100%;
}

/* header .keyboard {
  position: absolute;
  margin-top: 0%;
  margin-left: 41.50%;
} */

header .div-lupa {
  align-items: center;
  background-color: #313131;
  border-radius: 0px 3px 3px 0px;
  color:#6f6f6f;
  display: flex;
  height: 42px;
  justify-content: center;
  width: 58px;
}

header .buttons {
  display: flex;
}

header .microphone {
  height: 20px; 
}

header .div-microphone {
  align-items: center;
  background-color: #121212; 
  border-radius: 50%;
  display: flex;
  justify-content: center;
  height: 40px;
  margin-left: 10px;
  width: 40px;
}

header .video {
  height: 20px;
}

header .div-video {
  align-items: center;
  display: flex;
  height: 40px;
  justify-content: center;
  margin-right: 15px;  
  width: 40px;
}

header .grid {
  height: 20px;
}

header .div-grid {
  align-items: center;
  display: flex;
  height: 40px;
  justify-content: center;
  margin-right: 15px;  
  width: 40px;
}

header .bell {
  height: 20px;
}

header .div-bell {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 40px;
  margin-right: 15px;  
  width: 40px;
}

header .icon {
  border-radius: 50%;
  height: 35px;
}

header .div-icon {
  align-items: center;
  display: flex;
  height: 40px;
  justify-content: center;
  margin-right: 15px;  
  width: 40px;
}

.nav {
  margin-left: 75px;
}

a {
  text-decoration: none;
  color: white;
}

nav#menu ul {
  height:56px;
  list-style: none;
  border-top: 1px #373737 solid;
  border-bottom: 1px #373737 solid; 
  display: flex;
  align-items: center;
}

nav#menu li {
  display: flex;
  border: 1px solid #4a4a4a;
  border-radius: 20px;
  padding: 7px 12px;
  margin: 6px;
  transition: background-color 0.5s;
  background-color: #373737;
}

nav#menu li:hover {   
  background-color: #616161c4;
}

nav#menu li:nth-child(1) {
  background-color: #d2cfcf;
  margin-left: 21px;
}

nav#menu li:nth-child(1):hover {
  background-color: white;
}

.next {
  width: 40px;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.next img {
  height: 16px;
}

.all {
  color: #101010;
}

.all:hover {
  color: black;
}
main {
  background-color: #121212;
  margin-left: 75px;
  justify-content: space-around;
  display: flex;
  flex-wrap: wrap;
}

main .contents {
  margin: 20px 10px;
}

main .videos {
  border: 1px solid yellow;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 295.48px;
  height: 165.8px;
  color: white;
}

main .descrition {
  border: 1px solid green;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 295.48px;
  height: 94px;
  color: grey;
}
aside {
  position: fixed;
  top: 9%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 16px 0 14px;
  width: 72px;
}

aside div {
  margin-bottom: 24px;
}

aside div:hover {
  background-color: #616161c4;
}
